import en_dict from "./en/dictionary.json";
import en_targets from "./en/targetList.json";
import hr_dict from "./hr/dictionary.json";
import hr_targets from "./hr/targetList.json";

const DefaultTexts = {
  game: {
    tooShort: "Too short",
    notAValidWord: "Not a valid word",
    gameOver: (won: boolean, target: string, challenge: boolean) =>
      `You ${
        won ? "won" : "lost"
      }! The answer was ${target.toUpperCase()}. (Enter to ${
        challenge ? "play a random game" : "play again"
      })`,
    nmLetters: (length: number) => `${length} letters`,
    gaveUpHint: (target: string) =>
      `The answer was ${target.toUpperCase()}. (Enter to play again)`,
    giveUp: "Give up",
    resultCopiedToClipboard: "Result copied to clipboard!",
    linkCopiedToClipboard: "Link copied to clipboard!",
    playingChallenge: "playing a challenge game",
    playingRandom: "playing a random game",
    invalidChallengeError: "`Invalid challenge string, playing random game.`",
    makeYourFirstGuess: "Make your first guess!",
    letters: "Letters:",
  },
  settings: {
    difficulty: [
      `Guesses must be valid dictionary words.`,
      `Wordle's "Hard Mode". Green letters must stay fixed, and yellow letters must be reused.`,
      `An even stricter Hard Mode. Yellow letters must move away from where they were clued, and gray clues must be obeyed.`,
    ],
  },
};

interface Keyboard {
  keys: string;
  name: string;
}

export interface LanguageSettings {
  keyboards: Keyboard[];
  texts: typeof DefaultTexts;
  dictionary: string[];
  targetList: string[];
}

export const en: LanguageSettings = {
  keyboards: [
    { keys: "qwertyuiop-asdfghjkl-BzxcvbnmE", name: "QWERTY" },
    { keys: "azertyuiop-qsdfghjklm-BwxcvbnE", name: "AZERTY" },
    { keys: "qwertzuiop-asdfghjkl-ByxcvbnmE", name: "QWERTZ" },
    { keys: "BpyfgcrlE-aoeuidhtns-qjkxbmwvz", name: "Dvorak" },
    { keys: "qwfpgjluy-arstdhneio-BzxcvbkmE", name: "Colemak" },
  ],
  texts: DefaultTexts,
  dictionary: en_dict,
  targetList: en_targets.slice(0, en_targets.indexOf("murky") + 1),
};

export const hr: LanguageSettings = {
  keyboards: [
    { keys: "qwertzuiopšđ-asdfghjklčćž-ByxcvbnmE", name: "Croatian" },
  ],
  texts: {
    game: {
      tooShort: "Prekratak pokušaj",
      notAValidWord: "Nepoznata riječ",
      gameOver: (won: boolean, target: string, challenge: boolean) =>
        `${
          won ? "Pobijedili ste!" : "Izgubili ste!"
        } Odgovor je bio ${target.toUpperCase()}. (Pritisnite Enter za ${
          challenge ? "novu igru" : "ponovni pokušaj"
        })`,
      nmLetters: (length: number) => `${length} slova`,
      gaveUpHint: (target: string) =>
        `Odogovor je ${target.toUpperCase()}. (Pritisni Enter za ponovno igranje)`,
      giveUp: "Odustani",
      resultCopiedToClipboard: "Rezultat kopiran",
      linkCopiedToClipboard: "Link kopiran",
      playingChallenge: "igra izazova",
      playingRandom: "igra slučajnog odabira",
      invalidChallengeError:
        "Nevažeće inicijalne postavke, igra je slučajnog odabira",
      makeYourFirstGuess: "Pokušajte pogoditi!",
      letters: "Slova:",
    },
    settings: {
      difficulty: [
        `Pokušaji moraju biti riječi iz rječnika.`,
        `Teži mod. Zelena slova moraju ostati nepromijenjena, žuta se moraju koristiti.`,
        `Najteži mod. Žuta slova se moraju koristiti na drugom mjestu, siva slova se ne smiju više koristiti.`,
      ],
    },
  },
  dictionary: hr_dict.filter((w) => w.length > 2),
  targetList: hr_targets.filter((w) => w.length > 2),
};
