import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { Clue, clue, describeClue, violation } from "./clue";
import { Row, RowState } from "./Row";
import { Keyboard } from "./Keyboard";
import {
  describeSeed,
  Difficulty,
  getLanguageSettings,
  Language,
  pick,
  resetRng,
  seed,
  speak,
  urlParam,
} from "./util";
import { decode, encode } from "./base64";

enum GameState {
  Playing,
  Won,
  Lost,
}

interface GameProps {
  language: Language;
  maxGuesses: number;
  hidden: boolean;
  difficulty: Difficulty;
  colorBlind: boolean;
  keyboardLayout: string;
}

const minLength = 4;
const maxLength = 11;

function getChallengeUrl(target: string): string {
  return (
    window.location.origin +
    window.location.pathname +
    "?challenge=" +
    encode(target)
  );
}

function parseUrlLength(): number {
  const lengthParam = urlParam("length");
  if (!lengthParam) return 5;
  const length = Number(lengthParam);
  return length >= minLength && length <= maxLength ? length : 5;
}

function parseUrlGameNumber(): number {
  const gameParam = urlParam("game");
  if (!gameParam) return 1;
  const gameNumber = Number(gameParam);
  return gameNumber >= 1 && gameNumber <= 1000 ? gameNumber : 1;
}

function Game({
  language,
  maxGuesses,
  colorBlind,
  difficulty,
  hidden,
  keyboardLayout,
}: GameProps) {
  const { texts, dictionary, targetList } = useMemo(
    () => getLanguageSettings(language),
    [language]
  );
  const dictionarySet = useMemo(() => {
    return new Set(dictionary);
  }, [dictionary]);

  const randomTarget = useCallback(
    (wordLength: number): string => {
      const eligible = targetList.filter((word) => word.length === wordLength);
      let candidate: string;
      do {
        candidate = pick(eligible);
      } while (/\*/.test(candidate));
      return candidate;
    },
    [targetList]
  );

  let initChallenge = "";
  let challengeError = false;
  try {
    initChallenge = decode(urlParam("challenge") ?? "").toLowerCase();
  } catch (e) {
    console.warn(e);
    challengeError = true;
  }
  if (initChallenge && !dictionarySet.has(initChallenge)) {
    initChallenge = "";
    challengeError = true;
  }

  const [guesses, setGuesses] = useState<string[]>([]);
  const [gameState, setGameState] = useState(GameState.Playing);
  const [currentGuess, setCurrentGuess] = useState<string>("");
  const [challenge, setChallenge] = useState<string>(initChallenge);
  const [wordLength, setWordLength] = useState(
    challenge ? challenge.length : parseUrlLength()
  );
  const [gameNumber, setGameNumber] = useState(parseUrlGameNumber());
  const [target, setTarget] = useState(() => {
    resetRng();
    // Skip RNG ahead to the parsed initial game number:
    for (let i = 1; i < gameNumber; i++) randomTarget(wordLength);
    return challenge || randomTarget(wordLength);
  });
  const [hint, setHint] = useState<string>(
    challengeError
      ? texts.game.invalidChallengeError
      : texts.game.makeYourFirstGuess
  );

  const currentSeedParams = useCallback(
    () => `?seed=${seed}&length=${wordLength}&game=${gameNumber}`,
    [wordLength, gameNumber]
  );

  useEffect(() => {
    if (seed) {
      window.history.replaceState(
        {},
        document.title,
        window.location.pathname + currentSeedParams()
      );
    }
  }, [currentSeedParams]);

  const tableRef = useRef<HTMLTableElement>(null);

  const startNextGame = useCallback(() => {
    if (challenge) {
      // Clear the URL parameters:
      window.history.replaceState({}, document.title, window.location.pathname);
    }
    const newWordLength =
      wordLength >= minLength && wordLength <= maxLength ? wordLength : 5;
    setChallenge("");
    setWordLength(newWordLength);
    setTarget(randomTarget(newWordLength));
    setHint("");
    setGuesses([]);
    setCurrentGuess("");
    setGameState(GameState.Playing);
    setGameNumber((x) => x + 1);
  }, [challenge, wordLength, randomTarget]);

  async function share(copiedHint: string, text?: string) {
    const url = seed
      ? window.location.origin + window.location.pathname + currentSeedParams()
      : getChallengeUrl(target);
    const body = url + (text ? "\n\n" + text : "");
    if (
      /android|iphone|ipad|ipod|webos/i.test(navigator.userAgent) &&
      !/firefox/i.test(navigator.userAgent)
    ) {
      try {
        await navigator.share({ text: body });
        return;
      } catch (e) {
        console.warn("navigator.share failed:", e);
      }
    }
    try {
      await navigator.clipboard.writeText(body);
      setHint(copiedHint);
      return;
    } catch (e) {
      console.warn("navigator.clipboard.writeText failed:", e);
    }
    setHint(url);
  }

  const onKey = useCallback(
    (key: string) => {
      if (gameState !== GameState.Playing) {
        if (key === "Enter") {
          startNextGame();
        }
        return;
      }

      if (guesses.length === maxGuesses) return;

      if (key === "Backspace") {
        setCurrentGuess((guess) => guess.slice(0, -1));
        setHint("");
      } else if (key === "Enter") {
        if (currentGuess.length !== wordLength) {
          setHint(texts.game.tooShort);
          return;
        }
        if (!dictionarySet.has(currentGuess)) {
          setHint(texts.game.notAValidWord);
          return;
        }
        for (const g of guesses) {
          const c = clue(g, target);
          const feedback = violation(difficulty, c, currentGuess);
          if (feedback) {
            setHint(feedback);
            return;
          }
        }
        setGuesses((guesses) => guesses.concat([currentGuess]));
        setCurrentGuess((guess) => "");

        if (currentGuess === target) {
          setHint(texts.game.gameOver(true, target, !!challenge));
          setGameState(GameState.Won);
        } else if (guesses.length + 1 === maxGuesses) {
          setHint(texts.game.gameOver(false, target, !!challenge));
          setGameState(GameState.Lost);
        } else {
          setHint("");
          speak(describeClue(clue(currentGuess, target)));
        }
      } else if (key.length === 1) {
        setCurrentGuess((guess) =>
          (guess + key.toLowerCase()).slice(0, wordLength)
        );
        tableRef.current?.focus();
        setHint("");
      }
    },
    [
      texts.game,
      dictionarySet,
      challenge,
      difficulty,
      maxGuesses,
      gameState,
      currentGuess,
      wordLength,
      startNextGame,
      target,
      guesses,
    ]
  );

  useEffect(() => {
    const onKeyDown = (e: KeyboardEvent) => {
      if (!e.ctrlKey && !e.metaKey) {
        onKey(e.key);
      }
      if (e.key === "Backspace") {
        e.preventDefault();
      }
    };
    document.addEventListener("keydown", onKeyDown);
    return () => {
      document.removeEventListener("keydown", onKeyDown);
    };
  }, [onKey]);

  let letterInfo = new Map<string, Clue>();
  const tableRows = Array(maxGuesses)
    .fill(undefined)
    .map((_, i) => {
      const guess = [...guesses, currentGuess][i] ?? "";
      const cluedLetters = clue(guess, target);
      const lockedIn = i < guesses.length;
      if (lockedIn) {
        for (const { clue, letter } of cluedLetters) {
          if (clue === undefined) break;
          const old = letterInfo.get(letter);
          if (old === undefined || clue > old) {
            letterInfo.set(letter, clue);
          }
        }
      }
      return (
        <Row
          key={i}
          wordLength={wordLength}
          rowState={
            lockedIn
              ? RowState.LockedIn
              : i === guesses.length
              ? RowState.Editing
              : RowState.Pending
          }
          cluedLetters={cluedLetters}
        />
      );
    });

  return (
    <div className="Game" style={{ display: hidden ? "none" : "block" }}>
      <div className="Game-options">
        <label htmlFor="wordLength">{texts.game.letters}</label>
        <input
          type="range"
          min={minLength}
          max={maxLength}
          id="wordLength"
          disabled={
            gameState === GameState.Playing &&
            (guesses.length > 0 || currentGuess !== "" || challenge !== "")
          }
          value={wordLength}
          onChange={(e) => {
            const length = Number(e.target.value);
            resetRng();
            setGameNumber(1);
            setGameState(GameState.Playing);
            setGuesses([]);
            setCurrentGuess("");
            setTarget(randomTarget(length));
            setWordLength(length);
            setHint(texts.game.nmLetters(length));
          }}
        ></input>
        <button
          style={{ flex: "0 0 auto" }}
          disabled={gameState !== GameState.Playing || guesses.length === 0}
          onClick={() => {
            setHint(texts.game.gaveUpHint(target));
            setGameState(GameState.Lost);
            (document.activeElement as HTMLElement)?.blur();
          }}
        >
          {texts.game.giveUp}
        </button>
      </div>
      <table
        className="Game-rows"
        tabIndex={0}
        aria-label="Table of guesses"
        ref={tableRef}
      >
        <tbody>{tableRows}</tbody>
      </table>
      <p
        role="alert"
        style={{
          userSelect: /https?:/.test(hint) ? "text" : "none",
          whiteSpace: "pre-wrap",
        }}
      >
        {hint || `\u00a0`}
      </p>
      <Keyboard layout={keyboardLayout} letterInfo={letterInfo} onKey={onKey} />
      <div className="Game-seed-info">
        {challenge
          ? texts.game.playingChallenge
          : seed
          ? `${describeSeed(seed)} — length ${wordLength}, game ${gameNumber}`
          : texts.game.playingRandom}
      </div>
      <p>
        <button
          onClick={() => {
            share(texts.game.linkCopiedToClipboard);
          }}
        >
          Share a link to this game
        </button>{" "}
        {gameState !== GameState.Playing && (
          <button
            onClick={() => {
              const emoji = colorBlind
                ? ["⬛", "🟦", "🟧"]
                : ["⬛", "🟨", "🟩"];
              share(
                texts.game.resultCopiedToClipboard,
                guesses
                  .map((guess) =>
                    clue(guess, target)
                      .map((c) => emoji[c.clue ?? 0])
                      .join("")
                  )
                  .join("\n")
              );
            }}
          >
            Share emoji results
          </button>
        )}
      </p>
    </div>
  );
}

export default Game;
